export const DoorModuleComponent: React.FC<ModuleComponentProps<ModuleType.Door>> = ({ bot }) => {
  const body = bot.getFacet(KinematicBodyFacet)!;

  const [x] = bot.transform.position;
  const [min, max] = [
    body.initialPosition[0] + body.props.constrainX![0],
    body.initialPosition[0] + body.props.constrainX![1],
  ];
  const complete = x === min || x === max;

  const directionRef = useRef<'open' | 'close'>(x === max ? 'open' : 'close');
  const lastXRef = useRef(x);
  if (lastXRef.current < x) {
    directionRef.current = 'open';
  } else if (lastXRef.current > x) {
    directionRef.current = 'close';
  }

  const classMap = {
    [directionRef.current]: complete ? classes.complete : classes.running,
  };

  lastXRef.current = x;
  return (
    <>
      <DoorModuleStatic />
      <circle
        filter={`url(#${FILTER_GLOW_BEIGE_ID})`}
        cx="130"
        cy="330"
        r="13"
        fill="#fff"
        className={clsx(classes.light, classMap.close)}
      />
      <circle
        filter={`url(#${FILTER_GLOW_BEIGE_ID})`}
        cx="270"
        cy="330"
        r="13"
        fill="#fff"
        className={clsx(classes.light, classMap.open)}
      />
    </>
  );
};
