import { Engine } from 'engine/Engine';
import { Entity } from 'engine/Entity';
import { ColliderFacet, ColliderType } from 'engine/facets/ColliderFacet';
import { Facet } from 'engine/facets/Facet';
import { SpriteGroupFacet } from 'engine/facets/SpriteGroupFacet';
import { StaticSpriteFacet } from 'engine/facets/StaticSpriteFacet';
import { create } from 'engine/lib/create';
import { Activatable } from 'game/behaviors/Activatable';
import { FILTER_OUTLINE_ID } from 'game/defs/FilterOutline';
import { inGameUIClasses } from 'game/styles/inGameUI';
import { createElement } from 'react';
import { LabEntranceSprite, LabRailFgSprite, LabSprite, LabStatueSprite } from './LabSprite';

export class LabEntity extends Entity {
  createFacets() {
    return [
      create(StaticSpriteFacet, { frame: LabSprite }),
      create(ColliderFacet, { type: ColliderType.Box, offset: [2050, 2470], size: [40, 1000] }),
      create(ColliderFacet, { type: ColliderType.Box, offset: [1530, 2425], size: [560, 100] }),
      create(ColliderFacet, { type: ColliderType.Box, offset: [1530, 1530], size: [60, 1000] }),
      create(ColliderFacet, { type: ColliderType.Box, offset: [1580, 1530], size: [510, 100] }),
      create(ColliderFacet, { type: ColliderType.Box, offset: [2470, 1530], size: [510, 100] }),
      create(ColliderFacet, { type: ColliderType.Box, offset: [2970, 1530], size: [60, 1000] }),
      create(ColliderFacet, { type: ColliderType.Box, offset: [2470, 2425], size: [560, 100] }),
      create(ColliderFacet, { type: ColliderType.Box, offset: [2470, 2470], size: [40, 1000] }),
    ];
  }
}

export class LabRailFgEntity extends Entity {
  createFacets() {
    return [
      create(StaticSpriteFacet, { frame: LabRailFgSprite, offset: [0, -152] }),
    ];
  }
}

export class LabStatue extends Entity {
  createFacets() {
    return [
      create(StaticSpriteFacet, { frame: LabStatueSprite, offset: [0, -380] }),
      create(ColliderFacet, { type: ColliderType.Circle,
        radius: 60,
        offset: [0, -90],
      }),
      create(ColliderFacet, { type: ColliderType.Circle,
        radius: 60,
        offset: [128, -90],
      }),
      create(ColliderFacet, { type: ColliderType.Polygon,
        offset: [0, 0],
        points: [[64, -90], [124, -100], [184, -90], [184, 30], [124, 40], [64, 30]],
      }),
    ];
  }
}

interface LabEntranceProps {
  onEnter: () => void,
}

class LabEntranceBehavior extends Facet<LabEntranceProps> {
  update(delta: number, engine: Engine) {
    if (engine.input.keyDown.KeyE) {
      this.props.onEnter();
    }
  }
}

export class LabEntrance extends Entity<LabEntranceProps> {
  createFacets(props: LabEntranceProps) {
    const sprite = create(SpriteGroupFacet, {
      children: [create(StaticSpriteFacet, { frame: LabEntranceSprite, offset: [-190, -290] })],
    });
    const behavior = create(LabEntranceBehavior, props);
    let text: StaticSpriteFacet | null = null;

    return [
      sprite,
      create(ColliderFacet, { type: ColliderType.Box, offset: [-190, -100], size: [380, 100] }),
      create(Activatable, {
        activate: (active, engine) => {
          sprite.props.filter = active ? `url(#${FILTER_OUTLINE_ID})` : undefined;

          if (active) {
            text = create(StaticSpriteFacet, {
              frame: createElement('text', {
                className: inGameUIClasses.activatedText,
                y: -220,
              }, '[e] Enter'),
            });
            this.addFacet(text, engine);
            this.addFacet(behavior, engine);
          } else if (text) {
            this.removeFacet(text);
            this.removeFacet(behavior);
          }
        },
      }),
    ];
  }
}
