import { Entity } from 'engine/Entity';
import { ColliderFacet, ColliderType } from 'engine/facets/ColliderFacet';
import { StaticSpriteFacet } from 'engine/facets/StaticSpriteFacet';
import { create } from 'engine/lib/create';
import { LadderBackSprite, LadderFrontSprite } from './EndSprites';

export class LadderBack extends Entity {
  createFacets() {
    return [
      create(StaticSpriteFacet, { frame: LadderBackSprite, offset: [-95, -335] }),
      create(ColliderFacet, { type: ColliderType.Box, size: [125, 30], offset: [-95, -26] }),
    ];
  }
}

export class LadderFront extends Entity {
  createFacets() {
    return [
      create(StaticSpriteFacet, { frame: LadderFrontSprite, offset: [-125, -392] }),
      create(ColliderFacet, { type: ColliderType.Box, size: [125, 30], offset: [0, -15] }),
    ];
  }
}
