class GameManagerBehavior extends Facet {
  load(newRootState: RootState, engine: Engine) {
    engine.entities.forEach(e => {
      if (e !== this.entity) engine.destroy(e);
    });
    engine.add(create(MainWorld, {
      rootState: newRootState,
      reload: () => {
        engine.time.timeScale = 1;
        this.load(cloneDeep(rootState), engine);
      },
    }));
  }

  start(engine: Engine) {
    this.load(cloneDeep(rootState), engine);
  }
}

interface GameManagerProps {
  onStart: () => void,
}

export class GameManager extends Entity<GameManagerProps> {
  createFacets(props: GameManagerProps) {
    const titleScreen = create(UIFacet, { Component: () => null });
    const onStart = (engine: Engine) => {
      ambiencePrairie.play();
      this.addFacet(create(GameManagerBehavior, {}), engine);
      this.removeFacet(titleScreen);
      props.onStart();
    };
    titleScreen.props.Component = () => createElement(TitleScreen, { onStart });

    return [titleScreen];
  }
}
