import { Engine } from 'engine/Engine';
import { Entity } from 'engine/Entity';
import { ColliderFacet, ColliderType } from 'engine/facets/ColliderFacet';
import { Facet } from 'engine/facets/Facet';
import { SpriteGroupFacet } from 'engine/facets/SpriteGroupFacet';
import { StaticSpriteFacet } from 'engine/facets/StaticSpriteFacet';
import { create } from 'engine/lib/create';
import { Activatable } from 'game/behaviors/Activatable';
import { FILTER_OUTLINE_ID } from 'game/defs/FilterOutline';
import { AlertSprite } from 'game/sprites/AlertSprite';
import { TooltipSprite } from 'game/sprites/TooltipSprite';
import { inGameUIClasses } from 'game/styles/inGameUI';
import { colors } from 'game/styles/theme';
import { createElement } from 'react';
import { FranklinShadowSprite, FranklinSprite } from '../tent-int/TentIntSprite';
import { ConstructionHatBackSprite, ConstructionHatFrontSprite } from './EndSprites';

let hasMessage = true;

interface FranklinEndProps {
  onTalk: () => void,
}

class FranklinEndBehavior extends Facet<FranklinEndProps> {
  update(delta: number, engine: Engine) {
    if (engine.input.keyDown.KeyE) {
      this.props.onTalk();
    }
  }
}

export class FranklinEnd extends Entity<FranklinEndProps> {
  createFacets(props: FranklinEndProps) {
    const alertSprites = hasMessage ? [
      create(StaticSpriteFacet, {
        frame: TooltipSprite({
          opacity: 1,
          fill: colors.white,
          stroke: colors.black,
          strokeWidth: 2,
        }),
        offset: [0, -310],
      }),
      create(StaticSpriteFacet, {
        frame: AlertSprite({ className: inGameUIClasses.shake }),
        offset: [-13, -350],
      }),
    ] : [];
    const onTalk = () => {
      hasMessage = false;
      props.onTalk();

      // For some reason, removing these sprites causes Chrome to chug
      alertSprites.forEach(s => { s.props.opacity = 0; });
    };

    const sprite = create(SpriteGroupFacet, {
      children: [
        create(StaticSpriteFacet, { frame: ConstructionHatBackSprite, offset: [-33, -246] }),
        create(StaticSpriteFacet, { frame: FranklinSprite, offset: [-46, -220] }),
        create(StaticSpriteFacet, { frame: ConstructionHatFrontSprite, offset: [-33, -246] }),
      ],
    });
    const behavior = create(FranklinEndBehavior, { onTalk });
    let text: StaticSpriteFacet | null = null;

    return [
      create(StaticSpriteFacet, { frame: FranklinShadowSprite, offset: [-25, -142] }),
      sprite,
      ...alertSprites,
      create(ColliderFacet, { type: ColliderType.Box, size: [120, 200], offset: [-60, -150], trigger: true }),
      create(Activatable, {
        activate: (active, engine) => {
          sprite.props.filter = active ? `url(#${FILTER_OUTLINE_ID})` : undefined;

          if (active) {
            text = create(StaticSpriteFacet, {
              frame: createElement('text', {
                className: inGameUIClasses.activatedText,
                y: -110,
              }, '[e] Talk'),
            });
            this.addFacet(text, engine);
            this.addFacet(behavior, engine);
          } else if (text) {
            this.removeFacet(text);
            this.removeFacet(behavior);
          }
        },
      }),
    ];
  }
}
